@font-face {
    font-family: "Ginger";
    src: url("../fonts/Ginger.woff") format("woff"),
        url("../fonts/Ginger.ttf") format("truetype");
}
@import url("https://fonts.googleapis.com/css2?family=Heebo:wght@300;400&display=swap");

var(--main-color): #fa9f41;
var(--secondary-color): #fdefe5;
var(--tertiary-color): #000000;
var(--last-color): #ffffff;


body {
    font-family: "Heebo", sans-serif;
    font-weight: 300;
}
h1 {
    font-family: "Ginger", sans-serif;

    font-size: 80px;
    font-weight: 600;
    text-align: center;
}
h2 {
    font-family: "Ginger", sans-serif;

    font-size: 60px;
    font-weight: 600;
}
h3 {
    font-size: 50px;
}
h4 {
    font-size: 40px;
}
h5 {
    font-size: 30px;
}
h6 {
    font-size: 20px;
}
p {
    font-family: "Heebo", sans-serif;
    font-weight: 300;
    font-size: 17px;
}

.portfolio {
    min-height: 100vh;
    overflow: hidden;
    section {
        height: calc(100vh - 70px);
    }
}
